import React from 'react';
import DashBoard from './Components/DashBoard/DashBoard';
import Loading from './Components/Loading/Loading';
import './App.css';

function App() {
  const [Latest, setLatest] = React.useState()
  const [Historical, setHistorical] = React.useState()
  const [World_wide, setWorld_wide] = React.useState()
  const [Countries, setCountries] = React.useState()

  const api = {
    Latest: 'https://api.rootnet.in/covid19-in/stats/latest',
    Historical: 'https://api.rootnet.in/covid19-in/stats/history',
    World_wide: 'https://disease.sh/v3/covid-19/all',
    Countries: 'https://covid19.mathdro.id/api/countries'
  }

  React.useEffect(() => {
    const Request = async () => {
      await fetch(api.Latest)
        .then(response => response.json())
        .then(Latest => (setLatest(Latest)))
      await fetch(api.Historical)
        .then(response => response.json())
        .then(Historical => (setHistorical(Historical)))
      await fetch(api.World_wide)
        .then(response => response.json())
        .then(World_wide => (setWorld_wide(World_wide)))
      await fetch(api.Countries)
        .then(response => response.json())
        .then(Countries => (setCountries(Countries)))
    }
    Request()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <div className="App">
      {Latest !== undefined && Historical !== undefined && World_wide !== undefined && Countries !== undefined ? (
        <DashBoard Latest={Latest} countries={Countries.countries} Historical={Historical} Worldwide={World_wide} />
      ) : (
        <Loading />
      )}
    </div>
  );
}

export default App;
