import React from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import "./InputSelect.css";
import { AppBar, Toolbar, Typography } from '@mui/material';

const InputSelect = ({ data, setloco, setMulti, countries }) => {
    const [Loc, setLoc] = React.useState('World-Wide')
    const handleChange = (event) => {
        setLoc(event.target.value)

        if (event.target.value !== "India" && event.target.value !== "World-Wide") {

            if (event.target.value < 39) {
                setloco(event.target.value)
                setMulti("Indian")
            }
            if (event.target.value >= 40) {
                setMulti("Country")
                setloco((event.target.value / 40) - 1)
            }
        }
        else {
            setMulti()
            setloco(event.target.value)
        }
    };
    return (
        <AppBar className="Main_Input" color="info" position="sticky" >
            <Toolbar className="App_Header" >
                <Typography className="Title">
                    Covid Dashboard
                </Typography>
                <FormControl variant="standard" className="form-control" sx={{ m: 1, minWidth: 120 }}>
                    <InputLabel id="demo-simple-select-standard-label">Select State</InputLabel>
                    <Select
                        labelId="demo-simple-select-standard-label"
                        id="demo-simple-select-standard"
                        className="Select"
                        value={Loc}
                        onChange={handleChange}
                        label="Select State"
                    >
                        <MenuItem value="World-Wide">World Wide</MenuItem>
                        <MenuItem value="India">India</MenuItem>
                        <MenuItem value="" className='seperator' disabled>Indian States</MenuItem>
                        {data.regional.map((loc, i) => <MenuItem value={i} key={i}>{loc.loc}</MenuItem>)}
                        <MenuItem value="" className='seperator' disabled>Countries</MenuItem>
                        {countries.map((loc, i) => <MenuItem value={(i + 1) * 40} key={i}>{loc.name}</MenuItem>)}

                    </Select>
                </FormControl>
            </Toolbar>
        </AppBar>
    );
}

export default InputSelect;
