import './Widget.css';
import { Fade, Paper } from '@mui/material';
import React from 'react';
import CountUp from 'react-countup';
import Recovered from '../Images/cure.png';
import Active from '../Images/patient.png';
import Total from '../Images/totalcase.svg';
import DeathRate from '../Images/death.png';
import Recovery from '../Images/recovery.png';
import Deaths from '../Images/icons8-death-64.png';

const Widget = ({ Data, index, Name }) => {

    var _active = Data.active || Data.total - Data.deaths - Data.discharged || Data.totalConfirmed - Data.deaths - Data.discharged
    var _total = Data.total || Data.totalConfirmed || Data.cases
    var _deaths = Data.deaths
    var _deathRate = (Data.deaths / _total * 100).toFixed(2)
    var _recoved = Data.discharged || Data.recovered
    var _recoveryRate = ((Data.discharged || Data.recovered) / (Data.total || Data.totalConfirmed || Data.cases) * 100).toFixed(2)

    return (
        <Fade in={true} timeout={5000}>
            <Paper className={`Widget ${Name}`} elevation={20}>
                <h4>
                    {Name === "Death_Rate" ? ("Death Rate") : Name === "Recovery_Rate" ? ("Recoverey Rate") : (`${Name}`)}
                </h4>
                <span>
                    {index === 0 ? (
                        <h2 className="Value Inner">
                            <CountUp className="Value" end={_total} duration={1} separator=',' />
                            <img alt="Not Available" className="Icon" src={Total} />

                        </h2>
                    ) : index === 1 ? (
                        <h2 className="Value Inner">
                            <CountUp className="Value" start={0} end={_active} duration={1} separator=',' />
                            <img alt="Not Available" className="Icon" src={Active} />

                        </h2>
                    ) : index === 2 ? (
                        <h2 className="Value Inner">
                            <CountUp className="Value" start={0} end={_deaths} duration={1} separator=',' />
                            <img alt="Not Available" className="Icon" src={Deaths} />
                        </h2>
                    ) : index === 3 ? (
                        <h2 className="Value Inner">
                            <span><CountUp className="Value" start={0} end={_deathRate} duration={1} separator=',' />%</span>
                            <img alt="Not Available" className="Icon" src={DeathRate} />

                        </h2>
                    ) : index === 4 ? (
                        <h2 className="Value Inner">
                            <CountUp className="Value" start={0} end={_recoved} duration={1} separator=',' />
                            <img alt="Not Available" className="Icon" src={Recovered} />
                        </h2>
                    ) : index === 5 ? (
                        <h2 className="Value Inner">
                            <span><CountUp className="Value" start={0} end={_recoveryRate} duration={1} separator=',' />%</span>
                            <img alt="Not Available" className="Icon" src={Recovery} />

                        </h2>
                    ) : ("")}
                </span>
            </Paper>
        </Fade>
    );
}

export default Widget;
