import { Fade, Paper } from '@mui/material'
import React from 'react'
import InputSelect from './Select/InputSelect'
import WidgetMain from './Widgets/WidgetMain'
import './DashBoard.css'
import Chart from './Chart/Chart'

export default function DashBoard({ Latest, countries, Historical, Worldwide }) {
    const [Location, setLocation] = React.useState('World-Wide')
    const [data, setdata] = React.useState('World-Wide')
    const [Multi, setMulti] = React.useState('World-Wide')


    React.useEffect(() => {
        if (Multi === 'Country') {
            fetch(`https://disease.sh/v3/covid-19/countries/${countries[Location].iso3}`)
                .then(response => response.json())
                .then((Country) => {
                    setdata(Country)
                    console.log(Country)
                })
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [Location])

    return (
        <div className="outer" >
            <InputSelect setMulti={setMulti} setloco={setLocation} loc={Location} countries={countries} data={Latest.data} />
            <Paper className='MAIN' elevation={24} >
                <div in={true} timeout={5000}>
                    <span className='MainInner' >
                        <span>
                            {Location === 'World-Wide' ? (
                                <Paper className='Paper' elevation={20}>
                                    <Fade in={true} timeout={5000}>
                                        <img alt={`${Location} Flag`} className='MainInnerImg' src="https://images.squarespace-cdn.com/content/v1/5fa6b76b045ef433ae7b252e/1604765875569-MUAEJNXG2NL6E4VEORZ6/Flag_20x30.jpg" />
                                    </Fade>
                                </Paper>
                            ) : Location === 'India' ? (
                                <Paper className='Paper' elevation={20}>
                                    <Fade in={true} timeout={5000}>
                                        <img alt={`${Location} Flag`} className='MainInnerImg' src='https://disease.sh/assets/img/flags/in.png' />
                                    </Fade>
                                </Paper>

                            ) : Multi === 'Indian' ? (
                                <Paper className='Paper' elevation={20}>
                                    <Fade in={true} timeout={5000}>
                                        <img alt={`${Location} Flag`} className='MainInnerImg' src='https://disease.sh/assets/img/flags/in.png' />
                                    </Fade>
                                </Paper>

                            ) : data.countryInfo !== undefined ? (
                                <Paper className='Paper' elevation={20}>
                                    <Fade in={true} timeout={5000}>
                                        <img alt={`${Location} Flag`} className='MainInnerImg' src={data.countryInfo.flag} />
                                    </Fade>
                                </Paper>
                            ) : ("")}
                        </span>
                        <span>
                            {Location === 'World-Wide' ? (
                                <WidgetMain DATA={Worldwide} />
                            ) : Location === 'India' ? (
                                <WidgetMain DATA={Latest.data.summary} />
                            ) : Multi === 'Indian' ? (
                                <WidgetMain DATA={Latest.data.regional[Location]} />
                            ) : (
                                <WidgetMain DATA={data} />
                            )}
                        </span>
                    </span>
                    <h2 style={{ textAlign: 'center', padding: '15px' }}  >
                        {Location === 'World-Wide' ? (
                            <Chart Data={Worldwide} Loco='World Wide' />
                        ) : Location === 'India' ? (
                            <Chart Data={Latest.data.summary} Loco='India' />
                        ) : Multi === 'Indian' ? (
                            <Chart Data={Latest.data.regional[Location]} Loco="Indian" />
                        ) : (
                            <Chart Data={data} Loco={data.country} />
                        )}
                    </h2>

                </div>
            </Paper>
        </div>
    )
}
