// import { Fade } from '@mui/material';
import React from 'react';
import './Chart.css'
import { Bar } from 'react-chartjs-2';
import { Fade, Paper } from '@mui/material';

const Chart = ({ Loco, Data }) => {
    var _active = Data.active || Data.total - Data.deaths - Data.discharged || Data.totalConfirmed - Data.deaths - Data.discharged
    var _total = Data.total || Data.totalConfirmed || Data.cases
    var _deaths = Data.deaths
    var _recoved = Data.discharged || Data.recovered
    var Location = `Corona virus cases data for ${Data.loc || Loco}`
    const labels = ['Cases', 'Active', 'Deaths', 'Recovered'];

    const dete = {
        labels: labels,
        datasets: [{
            label: `Covid Data of ${Location}`,
            data: [_total, _active, _deaths, _recoved],
            backgroundColor: [
                'RGBA(23,162,184,0.5)',
                'RGBA(0,123,255,0.5)',
                'RGBA(220,53,69,0.5)',
                'RGBA(40,167,69,0.5)',

            ],
            borderColor: [
                '#17a2b8',
                '#007bff',
                '#dc3545',
                '#28a745',
            ],
            borderWidth: 2
        }]
    };

    return (
        <div>
            {Data.message === undefined ? (
                <Fade in={true} timeout={5000}>
                    <Paper elevation={24}>

                        <Bar data={dete} style={{ minHeight: '650px' }} options={{
                            maintainAspectRatio: false,
                            responsive: true, options: {
                                elements: {
                                    bar: {
                                        borderWidth: 1,
                                    }
                                }
                            },
                            plugins: {
                                legend: {
                                    display: false,
                                },
                                title: {
                                    display: true,
                                    text: Location,
                                    font: {
                                        size: 20,
                                        family: 'Varela Round'
                                    },
                                    padding: 15
                                },
                                scales: {
                                    yAxes: [{
                                        ticks: {
                                            beginAtZero: true
                                        }
                                    }]
                                }


                            }
                        }
                        } />
                    </Paper>
                </Fade>
            ) : ("")}

        </div>
    );
}

export default Chart;
