import { Alert, Fade, Paper } from '@mui/material';
import React from 'react';
import Widget from './Widget';

const WidgetMain = ({ DATA }) => {
    const classnames = ['Total', 'Active', 'Deaths', 'Death_Rate', 'Recovered', 'Recovery_Rate']
    return (
        <Fade in={true} timeout={2000}>
            <Paper className="Main_Widget" elevation={20}>
                {DATA.message === undefined ? (
                    classnames.map((Name, i) =>
                        <Widget index={i} key={i} Name={Name} Data={DATA} />
                    )) : (
                    <Fade in={true} timeout={4000} >
                        <Alert className='Alert' variant="filled" severity="info" color="warning">{DATA.message}</Alert>
                    </Fade>
                )}
            </Paper >
        </Fade>
    );
}

export default WidgetMain;
